import React, { useState } from "react";
import jsonData from "./json"; // JSON dosyanzn bulunduu dizine gre yolu dzenleyin



import "survey-core/defaultV2.min.css";




import { Serializer as SurveySerializer } from "survey-core";

// Daha sonra kodunuzda SurveySerializer olarak kullanabilirsiniz
SurveySerializer.addProperty("itemvalue", {
  name: "score:number"
});


const CheckboxForm = () => {
  // State tanmlamalar
  const [checkboxValues, setCheckboxValues] = useState({});

  // JSON verisini formData deikenine atayn
  const formData = jsonData;

  // Checkbox durumu deitike alacak fonksiyon
  const handleCheckboxChange = (elementName, choiceValue) => {
    setCheckboxValues((prevValues) => ({
      ...prevValues,
      [elementName]: choiceValue,
    }));
  };

  // Toplam puan hesapla
  const calculateTotalScore = () => {
    let totalScore = 0;

    formData.pages.forEach((page) => {
      page.elements.forEach((element) => {
        if (element.type === "checkbox") {
          element.choices.forEach((choice) => {
            const elementName = element.name;
            const selectedChoice = checkboxValues[elementName];

            if (selectedChoice === choice.value) {
              totalScore += parseInt(choice.value, 10);
            }
          });
        }
      });
    });

    return totalScore;
  };

  return (
    <div>
      {/* Checkbox'lar olutur ve durumlarn izle */}
      {formData.pages.map((page) => (
        <div key={page.name}>
          <p>{page.name}</p>
          {page.elements.map((element) => (
            <div key={element.name}>
              <p>{element.title}</p>
              {element.choices.map((choice) => (
                <label key={choice.value}>
                  <input
                    type="checkbox"
                    name={element.name}
                    value={choice.value}
                    onChange={() =>
                      handleCheckboxChange(element.name, choice.value)
                    }
                    checked={checkboxValues[element.name] === choice.value}
                  />
                  {choice.text}
                </label>
              ))}
            </div>
          ))}
        </div>
      ))}

      {/* Toplam puan gster */}
      <p>Total Score: {calculateTotalScore()}</p>
    </div>
  );
};

export default CheckboxForm;
